import React from 'react'
import { styled } from '@mui/system'
import PendingInvitationsListItem from './PendingInvitationsListItem'
import { connect } from 'react-redux'
const MainContainer = styled('div')({
  width: '100%',
  height: '22%',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  overflow: 'auto'
})

function PendingInvitationsList({ pendingFriendsInvitations }) {
  return (
    <MainContainer>{pendingFriendsInvitations !== undefined ? pendingFriendsInvitations.map(i => <PendingInvitationsListItem key={i._id} id={i._id} username={i.senderId.username} mail={i.senderId.mail} />) : []}</MainContainer>
  )
}

const mapStoreStateToProps = ({ friends }) => {
  return {
    ...friends,
  }
};

export default connect(mapStoreStateToProps)(PendingInvitationsList);
