import React, { useState } from 'react'
import { styled } from '@mui/system'
import ResizeRoomButton from './ResizeRoomButton'
import VideosContainer from './VideosContainer'
import RoomButtons from './Buttons/RoomButtons'

const MainContainer = styled('div')({
    position: 'absolute',
    borderRadius: '8px',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: '#202225'
})

const fullScreenRoomStyle = {
    width: '100%',
    height: '100vh'
}
const minimizedRoomStyle = {
    width: '30%',
    height: '40vh',
    bottom: 0,
    right: 0
}

function Room() {
    const [isRoomMinimized, setIsRoomMinimized] = useState(true)
    const roomResizeHandler = () => {
        setIsRoomMinimized(!isRoomMinimized)
    }
    return (
        <MainContainer style={isRoomMinimized ? minimizedRoomStyle : fullScreenRoomStyle}>
            <VideosContainer />
            <RoomButtons />
            <ResizeRoomButton handleRoomResize={roomResizeHandler} isRoomMinimized={isRoomMinimized} />
        </MainContainer>
    )
}

export default Room