import axios from 'axios'
import { logout } from './features/utils/auth'

const apiClient = axios.create({
    baseURL: 'http://localhost:5002',
    timeout: 1000
})

apiClient.interceptors.request.use(config => {
    const userDetails = localStorage.getItem('user')

    if (userDetails) {
        const token = JSON.parse(userDetails).token
        config.headers.authorization = "Bearer " + token
    }

    return config
}, err => {
    return Promise.reject(err)
})

export const login = async (data) => {
    try {
        return await apiClient.post('/api/auth/login', data)
    } catch (exception) {
        return {
            error: true,
            exception
        }
    }
}
export const register = async (data) => {
    try {
        return await apiClient.post('/api/auth/register', data)
    } catch (exception) {
        return {
            error: true,
            exception
        }
    }
}

//secure
export const sendFriendInvitation = async (data)=>{
    try{
        return await apiClient.post('/api/friends-invitation/invite', data)
    } catch(exception){
        console.log(exception)
        checkResponseCode(exception)
        return {
            error: true,
            exception
        }
    }
}

export const acceptFriendInvitation = async (data)=>{
    try{
        return await apiClient.post('/api/friends-invitation/accept', data)
    }catch(exception){
        console.log(exception)
        checkResponseCode(exception)
        return {
            error: true,
            exception
        }
    }
}
export const rejectFriendInvitation = async (data)=>{
    try{
        return await apiClient.post('/api/friends-invitation/reject', data)
    }catch(exception){
        console.log(exception)
        checkResponseCode(exception)
        return {
            error: true,
            exception
        }
    }
}

const checkResponseCode = exception => {
    const responseCode = exception?.response?.status
    if (responseCode) {
        (responseCode === 401 || responseCode === 403) && logout()
    }
}